/*Contact and About Styling*/
body:has(#contact){
    background-color: black;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_14___});
    background-size: cover;
    background-position: 50% 60%;
    color: white;
}

body:has(#about){
    background-color: black;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_15___});
    background-size: cover;
    color: white;
}

body:has(#contact) button,
body:has(#about) button {
    color: white;
}

#about {
    margin: 128px;
}

#contact .info,
#about .info {
    text-align: center;
    font-family: Montserrat, sans-serif;
    font-size: 1.25rem;
    margin: 24px;
}

body:has(#contact) #attribution,
body:has(#about) #attribution {
    position: absolute;
    left: 0;
    bottom: 0;
    color: white;
